@import "../../assets/styles/colors.scss";
@import "../../assets/styles/dimensions.scss";

.sidebar {
  display: none;
  height: 100vh;
  background-color: #151b1f;
}

@media (min-width: 768px) {
  .sidebar {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 29px 0 20.5px 0;
    width: $tablet-sidebar-width;

    img {
      width: 42.23px;
      cursor: pointer;
    }

    .language {
      font-size: 13px;
      width: 48px;
      height: 48px;
      color: $main-accent;
      border-radius: 48px;
      border: 1px solid transparent;
      background-color: transparent;
      margin: 0;

      &:hover {
        border-color: $gray-light;
        color: $gray-light;
      }

      &:active {
        color: $main-accent;
      }
    }
  }
}

@media (min-width: 1024px) {
  .sidebar {
    width: $desktop-sidebar-width;

    img {
      width: 32px;
    }
  }
}
